<template>
    <div>
        <!--{{$route.query.id}}-->
        <!--头部-->
        <div class="header common-bgcolor">
            <img src="../assets/top-logo.png">
            <span class="sort"></span>
        </div>
        <!--滑动导航组件-->
        <SlideNav :topMenu="topMenu" :actIndex="this.$route.query.id"></SlideNav>
        
        <!--子类导航-->
        <div class="sub-sort common-bgcolor">
            
            <img v-for="item in subSort" :src="item.pic" :key="item.pic">
               
        </div>
        
        <!--10点更新-->
        <div class="multi-block3">
            <router-link to="/">
                <img :src="multi_block3">
            </router-link>
        </div>

        <!--店铺展示组件-->
        <ShopShow :showData="showData"></ShopShow>

         <!--底部菜单模块-->
         <MenuBar :menubarData="menubarData"></MenuBar>
         <!--回到顶部组件-->
        <div class="back-top"><ToTop></ToTop></div>
    
    </div>
</template>


<script>
    //引入底部菜单模块
    import MenuBar from '../common/MenuBar'
    import SlideNav from '../common/SlideNav.vue'
    import ShopShow from '../common/ShopShow.vue'
     // 引入回到顶部组件
    import ToTop from '../pages/ToTop/ToTop.vue'
    export default {
        data(){
           return{
               //路由传过来的按钮下标
               id:(this.$route.query.id),
               //滑动导航数据
               topMenu:[],
               //滑动导航下方分类导航数据
               subSort:[],
               //10点更新数据
               multi_block3:'',
               jsonPage:1,
               //分类导航json请求参数数组
               jsonParams:[
                    {
                        unique:'',
                        cid:'312',
                        zy_id:'c3_l1_18_51_5',
                        platform:'m',
                        _:'1499758549650'
                    },
                    {
                        unique:'',
                        cid:'313',
                        zy_id:'c3_l1_18_51_5',
                        platform:'m',
                        _:'1499759333744',
                    },
                    { 
                        unique:'',
                        cid:'314',
                        zy_id:'c3_l1_18_51_5',
                        platform:'m',
                        _:'1499759378134',
                    },
                    {
                        unique:'',
                        cid:'1296',
                        zy_id:'c3_l1_18_51_5',
                        platform:'m',
                        _:'1499759396339',
                    },
                    {
                        unique:'',
                        cid:'1295',
                        zy_id:'c3_l1_18_51_5',
                        platform:'m',
                        _:'1499759535079',
                    },
                    {
                        unique:'',
                        cid:'316',
                        zy_id:'c3_l1_18_51_5',
                        platform:'m',
                        _:'1499759547844',
                    },
                    {
                        unique:'',
                        cid:'317',
                        zy_id:'c3_l1_18_51_5',
                        platform:'m',
                        _:'1499759560242',
                    },
                    {
                        unique:'',
                        cid:'318',
                        zy_id:'c3_l1_18_51_5',
                        platform:'m',
                        _:'1499759574522',
                    },
               ],
               //店铺滚动加载部分json请求参数
               jsonParams1:[
                    {
                        key:'312',
                        type:'6',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'gsortzxg',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:'1',
                        cm_cate:'312',
                    },
                    {
                        key:'313',
                        type:'6',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'gsortzxg',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:'1',
                        cm_cate:'313',
                    },
                    {
                        key:'314',
                        type:'6',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'gsortzxg',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:'1',
                        cm_cate:'314',
                    },
                    {
                        key:'1296',
                        type:'6',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'gsortzxg',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:'1',
                        cm_cate:'1296',
                    },
                    {
                        key:'1295',
                        type:'6',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'gsortzxg',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:'1',
                        cm_cate:'1295',
                    },
                    {
                        key:'316',
                        type:'6',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'gsortzxg',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:'1',
                        cm_cate:'316',
                    },
                    {
                        key:'317',
                        type:'6',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'gsortzxg',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:'1',
                        cm_cate:'317',
                    },
                    {
                        key:'318',
                        type:'6',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'gsortzxg',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:'1',
                        cm_cate:'318',
                    }
               ],
               //店铺部分数据
               showData:[]
           }
        },
        methods:{
            //获取滑动菜单数据
            getSlideNav(){
                this.$http.get("static/json/SlideNav.json").then(function(res){
                    console.log(res.body)
                    this.topMenu=res.body.homeNav
                })
            },
            //获取分类子导航数据
            getSubSort(id){
                this.$http.jsonp('https://jas.juanpi.com/pagetag/page',{
                    params:this.jsonParams[id-1]
                }).then(function(res){
                    var arr=res.body.module_ads.multi_block;
                    this.subSort=arr[0].data[0].child.concat(arr[1].data[0].child)
                    this.multi_block3=arr[2].data[0].child[0].pic;
                })

            },
            //获取店铺部分数据
            getShopData(id){
                this.jsonParams1[id-1].page=this.jsonPage;
                console.log(id-1)
                console.log(this.jsonParams1[id-1].page,'数组page');
                this.$http.jsonp('https://shop.juanpi.com/zxg',{
                    params:this.jsonParams1[id-1]
                }).then(function(res){
                    console.log(res.body)
                    this.showData=this.showData.concat(res.body.list)
                })
            },
            //滚动
            scrollLoad() {
                var _this = this;
                var base = 81 * parseFloat(document.documentElement.style.fontSize);
                var diff = 16 * parseFloat(document.documentElement.style.fontSize);
                window.onscroll = function() {
                    var dis = window.scrollTop || document.body.scrollTop || document.documentElement.scrollTop;
                    // console.log(dis, (dis - diff) / base);
                    if ((dis - diff) / base >= _this.jsonPage) {
                        _this.jsonPage++;
                        _this.getShopData(_this.id);
                    }
                    // console.log(_this.jsonPage, 'jsoppage');
                }
                
    
            }
        },
        watch:{
            "$route"(to,from){
                // console.log(to.query.id);
                this.getSubSort(to.query.id)//监听点击的按钮下标并重新jsonp请求数据
                this.getShopData(to.query.id)//根据id变化重新请求店铺数据
                this.showData=[];//店铺数据
                this.jsonPage=1;//id变化，表示页面更新，jsonpage重置为1
                
            }
        },
        components:{
            SlideNav,
            ShopShow,
            MenuBar,
            ToTop
        },
        computed:{
            menubarData:function(){
            return this.$store.getters.menubarData
            }
        },
        created(){
            window.onscroll =null ;
            this.getSubSort(this.id);
            this.getSlideNav();
            this.getShopData(this.id);
            this.scrollLoad();
        }
    }
</script>

<style lang="scss" scoped>
    .common-bgcolor{
        background-color: white;
    }
    .header{
        position:relative;
        height:1.5rem;
        padding-top: 0.1px;
    }
    .header>span{
        width:28px;
        height: 28px;
        position:absolute;
        right: .45rem;
        top: .35rem;
        background: url(../assets/fenlei.png)no-repeat;
        background-size:28px 28px;
        display: inline-block;
    }
    .header>img{
        height:.71rem;
        display: block;
        margin:.4rem auto;
    }


    .sub-sort{
        img{
            width:25%
        }
        margin-bottom:.5rem;
    }


    .multi-block3{
        background: white;
        a{display: block};
    }
    
    .multi-block3 {
        a{
            img{
                width: 100%;
            }
        }
    }
    .back-top>.slide-box{
        bottom: 3rem;
    }
</style>




